	top: 0;
}

#top-nav-buttons {
	border-radius: 90px;
	margin-left: 10px;
	margin-right: 10px;
	border: 1px solid #4158f6;
	color: #4158f6;
}

.info {
	position: relative;
	margin-top: 20px;
	/* height: 100%; */
	display: flex;
	align-items: center;
	justify-content: center;
}

h1 {
	line-height: 80%;
	color: #4056f7;
}

.post-card {
  background-size: cover;
  background-position-x: 50%;
  background-position-y: center;
  /* height: 500px;
  width: 300px;
  margin: 0px 10px 8px 10px; */
  border-radius: 25px;
