.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button_line {
  width: 30px;
  height: 2px;
  background: white;
}

.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: royalblue;
  height: 56px;
}

.toolbar_logo {
  margin-left: 1rem;
}

.toolbar_logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.toolbar_navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.toolbar_nav-items a {
  color: white;
  text-decoration: none;
}

.toolbar_nav-items ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.toolbar_nav-items li {
  padding: 0 0.5rem;
}

.toolbar_nav-items a:hover,
.toolbar_nav-items a:active {
  color: orange;
}

.spacer {
  flex: 1 1;
}

.dropdown-menu {
  position: absolute;
  z-index: 5000 !important;
  /* display: flex; */
}

@media (max-width: 768px) {
  .toolbar_nav-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar_toggle-button {
    display: none;
  }
  .toolbar_logo {
    margin-left: 0;
  }
}

.side-drawer {
  height: 100%;

  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  width: 70%;
  max-width: 250px;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.sideNavDrawerWrapper {
  box-sizing: border-box;
}
.side-drawer.open {
  transform: translateX(0);
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}

.sidebarWrapper {
  position: fixed;
  display: flex;
  /* width: 100%; */
  top: 0;
  left: 0;
}

#sidebar {
  min-width: 250px;
  max-width: 250px;
  min-height: 100vh;
}

#sidebar.active {
  margin-left: -250px;
}

a[data-toggle="collapse"] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

